import store from '@/store';
import { createRouter, createWebHashHistory } from 'vue-router';

const routes = [
    {//首页
        path: '/',
        name: 'home',
        component: () => import('@/views/Home.vue'),
    },
    {//实验目的
        path: '/purpose',
        name: 'purpose',
        component: () => import('@/views/Purpose.vue'),
    },
    {//实验原理
        path: '/principle',
        name: 'principle',
        component: () => import('@/views/Principle.vue'),
    },
    {//实验习题
        path: '/problem',
        name: 'problem',
        component: () => import('@/views/Problem.vue'),
    },
    {//实验仿真
        path: '/simulation',
        name: 'simulation',
        component: () => import('@/views/Simulation.vue'),
    },
    {//数据处理
        path: '/processing',
        name: 'processing',
        component: () => import('@/views/Processing.vue'),
    },
    {//实验结果
        path: '/result',
        name: 'result',
        component: () => import('@/views/Result.vue'),
    },
];

const router = createRouter({
    history: createWebHashHistory(process.env.BASE_URL),
    routes
});

let refresh = true;

router.beforeEach((to, from) => {
    if (refresh) {
        refresh = false;
        return '/';
    }
    if (to.path === '/simulation') {
        //window.isDevelopment 为 true 时, 跳过检测
        let notCan = store.state.score < 80 && !window.isDevelopment;
        if (notCan) {
            alert('习题分数不足80分，不能进行仿真');
        }
        return notCan ? from.path : true;
    }
    if (to.path === '/result') {
        //window.isDevelopment 为 true 时, 跳过检测
        let did = store.state.didExp || window.isDevelopment;
        if (!did) {
            alert('仿真暂未完成，不能查看结果');
        }
        return did ? true : from.path;
    }
    return true;
});

export default router;
